import React, { useEffect } from 'react'
import styles from './css/index.module.scss'

const Carousel = props => {
    const zoomDefault =  {
        zoomedIn: null,
        zoomIndex: null,
        zoomPower: 1,
        x: 0,
        y: 0,
        startX: null,
        startY: null,
    }
    const [cursorPosition, setCursorPosition] = React.useState({
          isDown: null,
          startX: null,
          cursorMove: 0,
          transform: 0,
          prevTransform: props.initialTransform,
          currentSlide: props.index,
          zoom: {...zoomDefault},
          pinchStart: {
              x1: 0,
              x2: 0,
              y1: 0,
              y2: 0,
          }
        });

    const zoomRef = React.useRef(null)
    const currentSlideRef = React.useRef(null)


    const slideTo = (index) => {
        const newTransfrom = (100 / props.images.length) * index
        setCursorPosition({
            ...cursorPosition,
            currentSlide: index,
            transform: newTransfrom,
            prevTransform: newTransfrom,
            zoom: {...zoomDefault}
        })
    }

    const dots = (imageList) => {
        if(!imageList || imageList.length < 0){return}
        const dots = imageList.map((image, index) => {
            return <li 
            onClick={() => {
                slideTo(index)
            }}
            key={`carouselDot-${index}`} 
            className={`${styles.dot} ${index === cursorPosition.currentSlide ? styles.dot_active : ""}`}
            ></li>
        })
        return <ul className={styles.dotList}>{dots}</ul>
    }

    const renderImages = (data) => {
        return data.map((image, index) => {
        return (
            <div
            className={styles.slide}
            ref={cursorPosition.currentSlide === image.index ? currentSlideRef : null }
            key={`${image}-${index}`}
            style={{width: `${100 / props.images.length}%`}}
            >                       
                <img 
                    ref={cursorPosition.currentSlide === image.index ? zoomRef : null}
                    style={cursorPosition.zoom.zoomedIn ? 
                        {transform: 
                        `scale(${cursorPosition.zoom.zoomPower}) 
                        translate(${cursorPosition.zoom.x}%, 
                        ${cursorPosition.zoom.y}%)`} 
                        : {}}
                    src={image.src}
                    alt={image.src}
                />
            </div>
        )
        })
    }

    const zoomIn = () => {
        console.log("ZOOM IN")
        let zoomedIn = null
        const countZoom = () => {
            let zoomPower = cursorPosition.zoom.zoomPower + 1
            zoomedIn = true
            if(zoomPower > 3){
                zoomPower = 1
                zoomedIn = false
            }
            return {zoomPower, zoomedIn}
        }
        const zoom = countZoom()
        return setCursorPosition({
            ...cursorPosition, 
            isDown: false,
            zoom: {
                ...cursorPosition.zoom,
                zoomedIn: zoom.zoomedIn,
                zoomPower: zoom.zoomPower,
                zoomIndex: cursorPosition.currentSlide,
                y: 0,
                x: 0
            }
        })
    }

    const zoomOut = () => {
        if(cursorPosition.zoom.zoomedIn){
            zoomRef.current.classList.add("smoothSlide")
            setTimeout(() => {
                zoomRef.current.classList.remove("smoothSlide")
            }, 100);
            setCursorPosition({
                ...cursorPosition,
                zoom: {...zoomDefault}
            })
        }
    }

        const startHandle = (e) => {
            e.preventDefault();
            if(e.touches && e.touches.length > 1){
                const container = document.querySelector(`.${styles.container}`)
                setCursorPosition({
                    ...cursorPosition,
                    pinchStart: {
                        x1: e.touches[0].pageX - container.offsetLeft,
                        x2: e.touches[1].pageX - container.offsetLeft,
                        y1: e.touches[0].pageY - container.offsetTop,
                        y2: e.touches[1].pageY - container.offsetTop,
                    }
                })
                return
            }
            const container = document.querySelector(`.${styles.container}`)
            document.querySelector(`.${styles.slideContainer}`).classList.remove("smoothSlide");
            const left = e.touches ? e.touches[0].pageX: e.pageX;

            setCursorPosition({
                ...cursorPosition,
                isDown: true,
                startX: left - container.offsetLeft
            })
          };

        const moveHandle = (e) => {
            if(e.touches && e.touches.length > 1){
                console.log(e.touches)
                const container = document.querySelector(`.${styles.container}`)

                const zoomPower = Math.hypot(
                    (e.touches[0].pageX - container.offsetLeft) - (e.touches[1].pageX - container.offsetLeft), 
                    (e.touches[0].pageY - container.offsetTop) - (e.touches[1].pageY - container.offsetLeft))

                setCursorPosition({
                    ...cursorPosition,
                    pinchSize: zoomPower
                })
                return
            }
            if(cursorPosition.zoom.zoomedIn){
                console.log("mouse move zoom in")
                e.preventDefault()
                if(window.innerWidth < 720){
                    document.querySelector("body").classList.add(styles.disableBodyScroll)
                }
            //   const slideContainer = document.querySelector(`.${styles.slideContainer}`)
            const slideContainer = currentSlideRef.current
            const zoomedImage = zoomRef.current
              const container = document.querySelector(`.${styles.container}`)
              const offset = {y: container.getBoundingClientRect().y, x: container.getBoundingClientRect().x, top: container.getBoundingClientRect().top}

              
              const imgToSlideHeightRatio = zoomedImage.clientHeight * 100 / slideContainer.clientHeight
              const imgToSlideWidthRatio = zoomedImage.clientWidth * 100 / slideContainer.clientWidth
              
              const visibleSectionY = imgToSlideHeightRatio / cursorPosition.zoom.zoomPower
              const marginY = ((100 - visibleSectionY) / 2) + 20
              const middleGuideY = slideContainer.clientHeight / 2
              let y = e.touches ? e.touches[0].clientY - offset.y : e.clientY - offset.y;
              let moveY = y - middleGuideY
              let panY = (moveY * marginY / middleGuideY).toFixed(2)

              const visibleSectionX = imgToSlideWidthRatio / cursorPosition.zoom.zoomPower
              const marginX = ((100 - visibleSectionX) / 2) + 20
              const middleGuideX = slideContainer.clientWidth / 2
              let x = e.touches ? e.touches[0].clientX - offset.x : e.clientX - offset.x
              let moveX = x - middleGuideX
              let panX = (moveX * marginX / middleGuideX).toFixed(2)

                return setCursorPosition({
                    ...cursorPosition, 
                    zoom:{...cursorPosition.zoom, 
                        y:panY, 
                        x: panX
                    }
                })
            }
            else if(!cursorPosition.isDown){return}
            console.log("mouse move")
              e.preventDefault();
              var slideContainer = document.querySelector(`.${styles.slideContainer}`);
            const container = document.querySelector(`.${styles.container}`)

              
              const x = e.touches ? e.touches[0].pageX - container.offsetLeft: e.pageX - container.offsetLeft
              const cursorMove = cursorPosition.startX - x
              var walk = 100 * (cursorMove) / slideContainer.clientWidth;
              var transform = walk + cursorPosition.prevTransform;

              setCursorPosition({
                ...cursorPosition,
                cursorMove,
                transform: transform,
              })
          };

          var endHandle = (e) => {
            const container = document.querySelector(`.${styles.slideContainer}`)
            container.classList.add("smoothSlide");
            if(!e.touches){
                if(cursorPosition.zoom.zoomedIn){ 
                    console.log("cursorPosition.zoom.zoomedIn")
                    zoomIn()
                    document.querySelector("body").classList.remove(styles.disableBodyScroll) 
                    return
                }
                if(!cursorPosition.isDown){
                    console.log("!cursorPosition.isDown")
                    return
                }
                if(Math.abs(cursorPosition.cursorMove) < 10){
                    console.log("cursorPosition.cursorMove < 10")
                    zoomIn()
                    return
                }
            }
            if(!cursorPosition.isDown){
                console.log("!cursorPosition.isDown")
                return
            }

            let direction = null
            direction = cursorPosition.cursorMove > 0 ? 1 : -1

            console.log({cursorMove: cursorPosition.cursorMove, direction})

            var positionFix = cursorPosition.transform;
            var index = null;
            var slideCount = props.images.length;
            var slideWidth = 100 / slideCount;

            if (positionFix > 0) {
              positionFix = 0;
              index = 0;
            } 
            if (positionFix < slideWidth - 100) {
                positionFix = slideWidth - 100;
                index = slideCount - 1;
            } 
            else {
            index = cursorPosition.currentSlide + direction;
                if(index < 0){index = 0}
                if(index > slideCount - 1){index = slideCount -1 }
                positionFix = (index) * (100 / slideCount);
            }
            setCursorPosition({
            ...cursorPosition,
            isDown: false,
            cursorMove: 0,
            transform: positionFix,
            prevTransform: positionFix,
            currentSlide: index
            })
          };



        useEffect(() => {
            setCursorPosition({
                ...cursorPosition,
                direction: null,
                transform: props.initialTransform,
                prevTransform: props.initialTransform,
                currentSlide: props.currentSlide,
                zoom:{...zoomDefault}
            })
          }, [props])

    return(
            <div className={styles.container}>
            <h3 className={styles.pinch}>Pinch: {cursorPosition.pinchSize}</h3>
            <h3 className={styles.pinch2}>Pinch start: 
            x1: {cursorPosition.pinchStart.x1}, 
            x2: {cursorPosition.pinchStart.x2}, 
            y1: {cursorPosition.pinchStart.y1}, 
            y2: {cursorPosition.pinchStart.y2}, 
            </h3>
            <div 
                id="slideContainer"
                className={styles.slideContainer}
                style={{
                    width: `${100 * props.images.length}%`,
                    transform: `translateX(${-cursorPosition.transform}%)`
                }}
                onMouseDown={(e) => {startHandle(e); console.log(e.type)} }
                onTouchStart={(e) => {startHandle(e); console.log(e.type)} }
                onMouseMove={(e) => {moveHandle(e); console.log(e.type)} }
                onTouchMove={(e) => {moveHandle(e); console.log(e.type)} }
                onMouseUp={(e) => {endHandle(e); console.log(e.type)} }
                onMouseOut={(e) => {endHandle(e); console.log(e.type); 
                    zoomOut()
                } }
                onTouchEnd={(e) => {endHandle(e);} }
            >
                {renderImages(props.images)}
            </div>
            <button 
            className={styles.button_zoom}
            onClick={() => {
                let zoomedIn = null
                const countZoom = () => {
                    let zoomPower = cursorPosition.zoom.zoomPower + 1
                    zoomedIn = true
                    if(zoomPower > 3){
                        zoomPower = 1
                        zoomedIn = false
                    }
                    return {zoomPower, zoomedIn}
                }
                const zoom = countZoom()
                setCursorPosition({
                    ...cursorPosition, 
                    zoom: {
                        ...cursorPosition.zoom,
                        zoomedIn: zoom.zoomedIn,
                        zoomPower: zoom.zoomPower,
                        zoomIndex: cursorPosition.currentSlide,
                        y: 0,
                        x: 0
                    }
                })
            }}>enlarge</button>
            {dots(props.images)}
        </div>
    )
}

export default Carousel


  